import checkBox from "@/components/checkboxRight.vue"
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	components:{
		checkBox,
		uniNumberBox
	},
	data() {
		return {
			cartList:[],   // 购物车模拟得数据
			allChecked: false,
			totalPrice: 0,   // 设置购物车总价格
			ischecked: false,
		}
	},
	onLoad() {
		this.getCartData()
	},
	created() {
		
	},
	methods: {
		// 获取购物车数据
		async getCartData(){
			let list =await this.$api.json('cartList')
			// 一开始让他全部不要选中商品
			list.map(item =>{
				item.check = false;
				return item
			})
			this.cartList = list
			console.log(this.cartList)
			this.setAllSelected()
		},
		// 点击选中与不选中
		check(type,index,ischecked){
			console.log(type,index,ischecked)
			if(type == 'item' && ischecked){
				this.cartList[index].check = true
			}else if(type == 'item' && !ischecked){
				this.cartList[index].check = false
			}else{
				if(type == 'all' && ischecked){
					this.cartList.forEach(item =>{
						item.check = true
					})
				}else{
					this.cartList.forEach(item =>{
						item.check = false
					})
				}
			}
			this.setAllSelected()
			this.calcTotal()
		},
		// 计算总价
		calcTotal(){
			let total = 0;
			this.cartList.forEach(item =>{
				if(item.check == true){
					total += item.price * item.number
				}
			})
			this.totalPrice = Number(total.toFixed(2))
		},
		// 单个商品加减计算
		numberChange(number,index){
			// console.log(number,index)
			this.cartList[index].number = number;
			this.calcTotal()
		},
		// 清空购物车
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				// 使用箭头函数，不然找不到this
				success:(e) =>{
					if(e.confirm){
						this.cartList = []
					}
				}
			})
		},
		// 删除单个商品
		deleteCartItem(index){
			this.cartList.splice(index,1)
		},
		// 结算按钮
		createOrder(){
			const goodsData = [];
			this.cartList.forEach(item =>{
				if(item.check == true){
					goodsData.push({
						attr_val: item.attr_val,
						number: item.number
					})
				}
			})
			if(goodsData.length<=0){
				uni.showModal({
					content:'请选择商品'
				})
			}else{
				console.log(goodsData)
				uni.switchTab({
					url: '../cart/cart'
				})
			}
		},
		// 判断所有选中，则全选
		setAllSelected(){
			console.log(this.cartList)
			if(this.cartList.length>0){
				let allCheckFlag = this.cartList.every(item => item.check == true)
				console.log(allCheckFlag)
				if(allCheckFlag){
					this.allChecked = true
				}else{
					this.allChecked = false
				}
			}	
		}
	}
}
